import React, { Component } from 'react';
import PropTypes from 'prop-types';
import classnames from 'classnames';
import { findIndex } from 'lodash';
import { setTransform } from './utils';

export default class TabContent extends Component {
  static PropTypes = {
    prefixCls: PropTypes.string
  }
  getTabPanes = () => {
    const props = this.props;
    const { activeKey, children, prefixCls,
    destroyInactiveTabPane } = props;
    const newChildren = [];
    React.Children.forEach(children, child => {
      if (!child) {
        return;
      }
      const key = child.key;
      const active = activeKey === key;
      newChildren.push(
        React.cloneElement(child, {
          active,
          rootPrefixCls: prefixCls,
          destroyInactiveTabPane
        })
      )
    });
    return newChildren;
  }
  render() {
    const props = this.props;
    const { prefixCls, animated, children, activeKey } = props;
    let { style } = props;
    const cls = classnames({
      [`${prefixCls}-content`]: true,
      [animated ? `${prefixCls}-content-animated` : `${prefixCls}-content-no-animated`]: true
    });
    const index = findIndex(children, child => child.key === activeKey);
    if (index > -1) {
      let contentStyle = {}
      setTransform(contentStyle, `translate3d(-${index * 100}%, 0, 0)`);
      style = {
        ...style,
        ...contentStyle
      }
    }
    return (
      <div
        className={cls}
        style={style}
        ref={node => { this.contentNode = node; }}
      >
        {
          this.getTabPanes()
        }
      </div>
    )
  }
}



