import React from 'react';
import PropTypes from 'prop-types';
// import cn from 'classnames';
/* eslint-disable no-unused-vars */
import Button from '../button';
/* eslint-disable no-unused-vars */
import Icon from '../icon';
import s from './style';
import LazyRenderBox from './lazyRenderBox';
import Animate from 'rc-animate';

function noop() {}

export default class Dialog extends React.Component {
  static defaultProps = {
    afterClose: noop,
    prefixCls: s.dialogPrefix,
    width: 520,
    onOk: noop,
    onCancel: noop,
    okText: '确定',
    cancelText: '取消',
    maskClosable: true,
    closable: true,
    className: ''
  }

  static propTypes = {
    prefixCls: PropTypes.string,
    title: PropTypes.oneOfType([PropTypes.string, PropTypes.node]),
    visible: PropTypes.bool,
    onOk: PropTypes.func,
    onCancel: PropTypes.func,
    okText: PropTypes.oneOfType([PropTypes.string, PropTypes.node]),
    maskClosable: PropTypes.bool
  }

  maskClose = (e) => {
    if (e.target === e.currentTarget) {
      this.onCancel();
    }
  }

  renderHeader = () => {
    const { prefixCls, title, header } = this.props;
    if (header !== undefined && (header === null || header === '')) {
      return ''
    } else {
      return <div className={`${prefixCls}-header`}>{header || title}</div>
    }
  }

  onCancel = () => {
    document.body.style.overflow = '';
    this.props.onCancel()
  }

  renderFooter = () => {
    const { prefixCls, footer, cancelText, okText } = this.props;
    const footerDom = footer;
    const defaultFooter = [(
      <Button
        key="cancel"
        key="cancel"
        onClick={this.onCancel}
      >
        {cancelText}
      </Button>
    ), (
        <Button
          type="primary"
          key="ok"
          onClick={() => this.props.onOk()}
        >
          {okText}
        </Button>
      )];
    return footer === '' || footer === null ? '' : <div className={`${prefixCls}-footer`}>{footer || defaultFooter}</div>
  }

  getMaskComponent = () => {
    const { visible, prefixCls } = this.props;
    return (
      <Animate
        key="mask"
        showProp="visible"
        component=""
        transitionName="fade"
        transitionAppear
      >
        <LazyRenderBox className={`${prefixCls}-mask`} hiddenClassName={`${prefixCls}-mask-hidden`} visible={visible}/>
      </Animate>
    )
  }

  getDialog = () => {
    const { visible, prefixCls, children, width, maskClosable, closable, className, style = {} } = this.props;
    const innerStyle = {
      width,
      ...style
    }
    return (
      <Animate
        key="dialog-con"
        showProp="visible"
        component=""
        transitionName="zoom"
        transitionAppear
        onEnd={() => { this.props.afterClose() }}
      >
        <LazyRenderBox key="dialog-element" className={`${prefixCls}-wrap`} hiddenClassName={`${prefixCls}-wrap-hidden`} visible={visible} onClick={maskClosable ? this.maskClose : null} >
          <div className={`${prefixCls} ${className}`} style={ innerStyle }>
            <div className={`${prefixCls}-content`}>
              {closable ? <button className={`${prefixCls}-close`} onClick={() => this.onCancel()}><Icon type='cross'/></button> : ''}
              {this.renderHeader()}
              <div className={`${prefixCls}-body`}>
                {children}
              </div>
              {this.renderFooter()}
            </div>
          </div>
        </LazyRenderBox>
      </Animate>
    )
  }

  render = () => {
    const { visible } = this.props;
    if (visible) {
      document.body.style.overflow = 'hidden';
    } else {
      document.body.style.overflow = '';
    }
    return [this.getMaskComponent(), this.getDialog()];
  }
}



