import React from 'react';
import Icon from '../icon';
import PropTypes from 'prop-types';
import _findIndex from 'lodash/findIndex';
import s from './style';
import message from '../message';
import Button from '../button'
const prefixCls = s.uploadPrefix;
function toArray(value) {
  let newValue = value;
  if (!value) {
    newValue = [];
  } else if (!Array.isArray(value)) {
    newValue = [value];
  }
  return newValue;
}

function noop() {}
class Upload extends React.Component {
  static defaultProps = {
    prefixCls,
    onChange: noop,
    multiple: false,
    fileList: '',
    note: '',
    showType: 'path'
  }
  static PropTypes = {
    accept: PropTypes.oneOf([PropTypes.string, PropTypes.Array]),
    fileList: PropTypes.oneOf([PropTypes.string, PropTypes.Array]),
    onChange: PropTypes.func,
    note: PropTypes.string,
    showType: PropTypes.string
  }
  constructor(props) {
    super(props);
    let fileList = toArray(props.fileList);
    let accept = toArray(props.accept);
    this.state = {
      fileList,
      accept
    }
  }
  componentWillReceiveProps(nextProps) {
    if ('fileList' in this.props) {
      this.setState({
        fileList: toArray(nextProps.fileList)
      })
    }
  }
  handleFileChange = e => {
    const target = e.target;
    const value = target.value;
    const { multiple } = this.props;
    let { accept, fileList } = this.state;
    let count = 0;
    if (value) {
      const filePath = value.slice(0, value.lastIndexOf('\\'));
      const file = Array.prototype.slice.call(target.files)[0];
      const index = _findIndex(accept, item => file.name.endsWith(item));
      if (index === -1 && accept.length) {
        message.warning('文件格式错误');
        return;
      }
      file.path = `${filePath}${'\\'}${file.name}`;
      if (multiple) {
        fileList.push(file);
      } else {
        fileList = [file];
      }
      if (!('fileList' in this.props)) {
        this.setState({
          fileList
        })
      }
      this.props.onChange(multiple ? fileList : fileList[0]);
    }
  }
  handleClick = index => {
    let { fileList } = this.state;
    const { multiple } = this.props;
    fileList.splice(index, 1);
    if (!multiple) {
      fileList = '';
    }
    if (!('fileList' in this.props)) {
      this.setState({fileList});
    }
    this.props.onChange(fileList);
  }

  render() {
    const { prefixCls, note, children, showType } = this.props;
    const { fileList } = this.state;
    return [
      <div className={`${prefixCls}-file-select`} key='select'  onClick={()=> {this.refs.file.click()}}>
        <input ref="file" type='file' style={{display: 'none'}} name='file' value='' className={`${prefixCls}-file-btn`} onChange={this.handleFileChange}/>
        {  children || <Button icon="upload">选择文件</Button> }
      </div>,
      <div className={`${prefixCls}-file-note`} key='note'>{note}</div>,
      <div className={`${prefixCls}-file-list`} key='list'>
        {
          (fileList || []).map((file, index) => <div className={`${prefixCls}-file-single`} key={index}>
            <Icon type="accessory" />
            {file[showType]}
            <Icon type='cross' className='close' onClick={() => this.handleClick(index)}/>
          </div>)
        }
      </div>
    ]
  }
}

export default Upload;



