import React from 'react';
import assign from 'object-assign';
import Icon from '../icon';
import Button from '../button';
import Tooltip from '../tooltip';
import s from './style/index';

export default class Popconfirm extends React.Component {
  static defaultProps = {
    prefixCls: s.tooltipPrefix,
    placement: 'top',
    okText: '确认',
    cancelText: '取消',
    content: '确认点击吗？'
  };

  constructor(props) {
    super(props);
    this.state = {
      visible: props.visible || false
    }
  }

  onConfirm = () => {
    this.setState({
      visible: false
    })
    if ('onConfirm' in this.props) {
      this.props.onConfirm();
    }
  }

  onCancel = () => {
    this.setState({
      visible: false
    })
    if ('onCancel' in this.props) {
      this.props.onCancel();
    }
  }

  getPopupElement = () => {
    const { arrowContent, content, prefixCls, okText, cancelText } = this.props;
    return ([
      <div className={`${prefixCls}-arrow`} key="arrow">
        {arrowContent}
      </div>,
      <div className={`${prefixCls}-inner`} key="inner">
        <div className={`${prefixCls}-inner-content`}>
          <Icon type="waring" /> {content}
        </div>
        <div className={`${prefixCls}-inner-content-btn`}>
          <Button type="default" size="small" onClick={this.onCancel}>{cancelText}</Button>
          <Button type="primary" size="small" onClick={this.onConfirm}>{okText}</Button>
        </div>
      </div>]
    )
  }

  handleVisibleChange = (visible) => {
    this.setState({ visible });
  }

  render() {
    const props = assign({}, this.props);
    delete props.title;
    delete props.visible;
    return (
      <Tooltip
        visible={this.state.visible}
        action="click"
        popup={this.getPopupElement}
        onVisibleChange={this.handleVisibleChange}
        {...props}
      />
    );
  }
}



